import React, { useState, useEffect } from "react";
import { Typography } from "@mui/material";
import { diasDeMembresia } from "../../api/Ejericios";

const DiasDeMembresia = ({ userId }) => {
  const [diasMembresa, setDasMembresa] = useState(null);
  const [mensaje, setMensaje] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    const obtenerDasDeMembresa = async () => {
      console.log("userId:", userId);
      if (!userId) {
        setError('El ID de usuario es invlido');
        return;
      }
      try {
        const response = await diasDeMembresia(userId);
        if (response?.data) {
          const { remainingDays, message } = response.data;
          setDasMembresa(remainingDays);
          setMensaje(message);
          if (remainingDays < 0) {
            setError("No tienes membresas pagadas.");
          }
        } else {
          setError("No se recibi la respuesta esperada.");
        }
      } catch (err) {
        setError("Error al obtener los das de membresa.");
        console.error("Error al obtener los das de membresa:", err.response ? err.response.data : err.message);
      }
    };
    obtenerDasDeMembresa();
  }, [userId]);

  return (
    <div>
      {error ? (
        <Typography variant="subtitle1" color="error">
          {error}
        </Typography>
      ) : (
        <Typography variant="subtitle1" component="p" sx={{ display: 'flex', alignItems: 'center' }}>
          <Typography
            variant="subtitle1"
            component="span"
            sx={{
              color: 'black',
            }}
          >
            Das de membresa:{" "}
          </Typography>
          
        <Typography
        variant="subtitle1"
        component="span"
        sx={{
          color: (diasMembresa !== null && !isNaN(diasMembresa) && diasMembresa < 0) ? 'red' : 'black',
          fontWeight: 'bold',
          marginLeft: 1,
          
        }}
      >
        {diasMembresa !== null ? diasMembresa : "Cargando..."}
      </Typography>
       </Typography>
      )}
      {mensaje && (
        <Typography variant="subtitle2" color="textSecondary">
          {mensaje}
        </Typography>
      )}
    </div>
  );
};

export default DiasDeMembresia;

